.header {
	position: fixed;
	z-index: 9999;
	top: 0;
	left: 0;
	width: 100%;

	.toolbar-horizontal & {
		left: 67px;
		width: calc(100% - 66px) !important;
	}
	a {
		transition: all 0.5s ease 0s;
		&:hover{
			color: #0047FF;
			&::before{
				background-color: #0047FF!important;
			}
		}
	}
	&__inner {
		backdrop-filter: blur(0px);
		-webkit-backdrop-filter: blur(0px);
		padding: 50px 0;
		transition: margin 0.5s, background-color 0.5s;
		@media (max-width: 1919px){
			padding: 40px 0;
		}
		@media (max-width: 1439px){
			padding: 38px 0;
		}
		@media (max-width: 1024px) {
			padding: 2.441vw 0;
		}
		@media (max-width: 767px){
			padding: vw(15,320) 0;
		}
	}
	&__container {
		display: flex;
		align-items: center;
	}
	&__logo {
		flex: 1 1 auto;
		display: flex;
		align-items: center;
		@media (max-width: 767px){
			justify-content: space-between;
		}
		svg {
			display: block;
			@media (max-width: 1919px){
				width: 90px;
				height: 20px;
			}
			@media (max-width: 1439px){
				width: 80px;
				height: 18px;
			}
			@media (max-width: 1024px) {
				width: 7.813vw;
				height: 1.758vw;
			}
			@media (max-width: 767px){
				width: 17.067vw;
				height: 3.733vw;
			}
			path {
				transition: all 0.5s ease 0s;
				fill: black;
			}
			&:hover{
				path {
					fill: #0047FF!important;
				}
			}
		}
	}

	&__burger {
		display: none;
		@media (max-width: 1919px){
			cursor: pointer;
			margin-left: 150px;
			font-family: 'Suisse Intl';
			font-style: normal;
			font-weight: 400;
			font-size: 14px;
			line-height: 16px;
			color: #2A2A2A;
			position: relative;
			display: block;
			transition: all 0.5s ease 0s;
			span {
				position: relative;
				height: 12px;
				width: 54px;
				transition: all 0.3s ease 0s;
				&:first-child{
					display: inline-block;
					margin-right: 9px;
					@media (max-width: 767px){
						margin-right: 0;
					}
					&::before,&::after{
						content:'';
						position: absolute;
						background-color: #000000;
						width: 100%;
						height: 2px;
						left: 0;
						transition: all 0.5s ease 0s;
					}
					&::before{
						top: 0;
					}
					&::after{
						bottom: 0;
					}
				}

			}
			&:hover{
				color: #0047FF!important;
				span {
					&::before, &::after {
						background-color: #0047FF!important;
					}
				}
			}
		}
		@media (max-width: 1919px){
			margin-left: 100px;
			span {
				&:nth-child(2){
					display: none;
				}
			}
		}
		@media (max-width: 1024px){
			margin-left: 5.859vw;
		}
		@media (max-width: 767px){
			margin-left: 16vw;
			span {
				width: 14.400vw;
				height: 3.200vw;
			}
		}
		&.header__burger-active {
			span {
				width: 24px;
				@media (max-width: 1024px){
					width: 2.344vw;
				}
				@media (max-width: 767px){
					width: 6.400vw;
				}
				&::before,&::after{
					content:'';
					top: 50%!important;
					bottom: auto!important;
				}
				&::before{
					transform: rotate(45deg);
				}
				&::after{
					transform: rotate(-45deg);
				}
			}
		}
	}

	&__right {
		transition: all 0s ease 0s;
		flex: 0 1 65%;
			display: flex;
			align-items: center;
			justify-content: space-between;
			font-family: 'Suisse Intl';
			font-style: normal;
			font-weight: 400;
			font-size: 18px;
			line-height: 21px;
			color: black;
			@media (max-width: 1919px){
				flex: 0 1 65%;
				font-size: 14px;
				line-height: 16px;
			}
			@media (max-width: 1439px){
				flex: 0 1 65%;
				font-size: 14px;
				line-height: 16px;
				margin-left: -35px;
			}
			@media (max-width: 1024px){
				font-size: 1.367vw;
				line-height: 1.563vw;
				margin-left: -3.418vw;
			}
			@media (max-width: 767px){
				display: none;
			}
		}

		&__menu {
			@media (max-width: 767px){
				display: none;
			}
		}

		&__connection {
			display: flex;
			align-items: center;
			transition: all 0.5s ease 0s;
			&--tel {
				white-space: nowrap;
			}
			a {
				margin-left: 50px;
				@media (max-width: 1919px){
					margin-left: 40px;
				}
				@media (max-width: 1439px){
					margin-left: 55px;
				}
				@media (max-width: 1024px) {
					margin-left: 5.371vw;
				}
				@media (max-width: 767px){
					margin-left: 14.667vw;
				}
			}
		}
		&.js-header-white {
			.header__logo {
				svg {
					path {
						fill: white;
					}
				}
			}
			.header__burger {
				color: #ffffff;
				span {
					&:first-child{
						&::before,&::after{
							content:'';
							background-color: #ffffff;
						}
					}
				}
			}
			.header__right {
				color: white;
				.menu-header__item.active::before{
					background: #fff;
				}
			}
			&-dark {
				.header__logo {
					svg {
						path {
							fill: #2A2A2A;
						}
					}
				}
				.header__burger {
					color: #2A2A2A;
					span {
						&:first-child {
							&::before,
							&::after {
								content: '';
								background-color: #000000;
							}
						}
					}
				}
				.header__right {
					color: #2A2A2A;
				}
				a.menu-mobile__link-active-white::before {
					background-color: #000000;
				}
			}
		}
		&.js-header-fixed {
			position: fixed;
			width: 100%;
			.menu-header__item.active::before  {
				bottom: -41px;
				@media (max-width: 1919px){
					bottom: -28px;
				}
				@media (max-width: 1439px){
					bottom: -28px;
				}
			}
			.header__inner {
				background-color: rgba(255, 255, 255, 0.2);
				backdrop-filter: blur(24px);
				-webkit-backdrop-filter: blur(24px);
				margin: 20px 30px 0;
				padding: 39px 0;
				@media (max-width: 1919px){
					margin: 20px 20px 0;
					padding: 26px 0;
				}
				@media (max-width: 1439px){
					margin: 20px 30px 0;
					padding: 27px 0;
				}
				@media (max-width: 1024px) {
					margin: 2vw 2.930vw 0;
					padding: vw(13,1024) 0;
				}
				@media (max-width: 767px){
					margin: 5.333vw 5.333vw 0;
					padding: vw(8,320) 0;

				}
				.header__right {
					flex: 0 1 65.2%;
					@media (max-width: 1919px) {
						flex: 0 1 64.2%;
					}
					@media (max-width: 1439px) {
						flex: 0 1 67%;
					}
				}
				.menu-header__list {
					@media (max-width: 1919px){
						margin-left: -50px;
					}
					@media (max-width: 1439px){
						margin-left: -20px;
					}
				}
			}
		}

		&.header-mobile_active {
			background: #2A2A2A;
			.menu-header__item.active::before {
				@media (max-width: 1439px){
					bottom: -38px;
				}
			}
			&:after{
				content: '';
				border-top: 1px solid rgba($color: #ffffff, $alpha: 0.2);
				position: absolute;
				left:20px;
				right:20px;
				bottom:0;
				@media (max-width: 1439px){
					left:30px;
					right:30px;
				}
				@media (max-width: 1024px){
					left:2.93vw;
					right:2.93vw;
				}
				@media (max-width: 767px){
					left:5.333vw;
					right:5.333vw;
				}
			}
			.header__inner {
				margin: 0!important;
				background-color: transparent;
				backdrop-filter: blur(0);
				-webkit-backdrop-filter: blur(0px);
				// .header__right {
				// 	flex: 0 1 65.2%;
				// 	@media (max-width: 1919px) {
				// 		flex: 0 1 64.2%;
				// 	}
				// 	@media (max-width: 1439px) {
				// 		flex: 0 1 67%;
				// 	}
				// }
				.menu-mobile__link-active {
					&::before{
						background-color: #ffffff;
					}
				}
												.header__logo {
													svg {
														path {
															fill: white;
														}
													}
												}

												.header__burger {
													color: #ffffff;

													span {
														&:first-child {

															&::before,
															&::after {
																content: '';
																background-color: #ffffff;
															}
														}
													}
												}

												.header__right {
													color: white;
												}

												&-dark {
													.header__logo {
														svg {
															path {
																fill: #2A2A2A;
															}
														}
													}

													.header__burger {
														color: #2A2A2A;

														span {
															&:first-child {

																&::before,
																&::after {
																	content: '';
																	background-color: #000000;
																}
															}
														}
													}

													.header__right {
														color: #2A2A2A;
													}
												}
			}
			.menu-header__item.active::before{
				background: #fff;
			}
		}

}

.menu-header {
	margin: 0;
	transition: all 0.5s ease 0s;
	&__list {
		display: flex;
		align-items: center;
		margin-left: -45px;
		@media (max-width: 1919px){
			margin-left: -35px;
		}
		@media (max-width: 1024px){
			margin-left: -3.418vw;
		}

	}

	&__item {
		position: relative;
		&.active {
			&::before {
				position: absolute;
				bottom: -56px;
				width: 100%;
				height: 3px;
				content: "";
				display: block;
				background: #000;
				@media (max-width: 1919px){
					bottom: -43px;
				}
				@media (max-width: 1439px){
					bottom: -44px;
				}
				@media (max-width: 1024px) {
					padding: 2.441vw 0;
				}
				@media (max-width: 767px){
					padding: vw(15,320) 0;
				}
			}
		}
		margin-left: 45px;
		@media (max-width: 1919px){
			margin-left: 35px;
		}
		@media (max-width: 1024px) {
			margin-left: 3.418vw;
		}
		@media (max-width: 767px){
			margin-left: 9.333vw;
		}
		// &-hide {
		// 	@media (max-width: 1439px){
		// 		display: none;
		// 	}
		// }
		&:nth-child(1) {
			@media (max-width: 1224px){
				display: none;
			}
		}
		&:nth-child(2) {
			@media (max-width: 1224px){
				display: none;
			}
		}
		&:nth-child(3) {
			@media (max-width: 1224px){
				display: none;
			}
		}
		&:nth-child(4) {
			@media (max-width: 1279px){
				display: none;
			}
		}
		&:nth-child(5) {
			@media (max-width: 1279px){
				display: none;
			}
		}
		&:nth-child(6) {
			@media (max-width: 1365px){
				display: none;
			}
		}
		&:nth-child(7) {
			@media (max-width: 1439px){
				display: none;
			}
		}
	}

}

.menu-mobile::-webkit-scrollbar {
	width: 0;
}
