.services {
	padding: 45px 0 167px;

	@media (max-width: 1919px) {
		padding: 30px 0 167px;
	}

	@media (max-width: 1439px) {
		padding: 30px 0 167px;
	}

	@media (max-width: 1024px) {
		padding: 2.930vw 0 11.719vw;
	}

	@media (max-width: 767px) {
		padding: 4vw 0 13.333vw;
	}

	.services__item--wrapCalc {
		.services__item-right {
			display: flex;
			justify-content: flex-start;
			align-items: flex-start;
			flex-flow: column nowrap;

			@media (max-width: 767px) {
				flex: 0 1 100%;
				width: 100%;
			}
		}

		.services__item--calc {
			position: relative;
			display: inline-block;
			width: 100%;
			max-width: 550px;

			@media (max-width: 767px) {
				max-width: 100%;
			}

			span {
				opacity: 0.4;
			}

			&__button {
				color: #0047FF;

				@media (max-width: 767px) {
					margin-top: vw(27, 320);
				}
			}
		}
	}

	&__items {
		// padding-bottom: 120px;

		// min-height: 100vh;
		// @media (max-width: 1919px) {
		// 	padding-bottom: 80px;
		// }

		@media (max-width: 1024px) {
			padding-bottom: 0;
			min-height: 0;
		}

		@media (max-width: 767px) {
			padding-bottom: 0;
		}
	}

	&__item-wrapper {
		display: block;
		position: relative;

		&:not(:last-child) {
			border-bottom: 1px solid rgba($color: #000000, $alpha: 0.2);
			padding-bottom: 30px;

			@media (max-width: 1919px) {
				padding-bottom: 25px;
			}

			@media (max-width: 1024px) {
				padding-bottom: 2.441vw;
			}

			@media (max-width: 767px) {
				border-bottom: 0.267vw solid rgba($color: #000000, $alpha: 0.2);
				padding-bottom: 4vw;
			}
		}

		&:not(:first-child) {
			margin-top: 40px;

			@media (max-width: 1919px) {
				margin-top: 30px;
			}

			@media (max-width: 1024px) {
				margin-top: 2.930vw;
			}

			@media (max-width: 767px) {
				margin-top: 6.667vw;
			}
		}

		&.opacity {
			.services__item-right {
				padding-left: 0px;

				// @media (max-width: 1024px){
				// 	padding-left: 0.977vw;
				// }
				span {
					opacity: 0.4;
					color: #2a2a2a;
				}
			}
		}

		// &:hover {
		// 	.services__item-right {
		// 		padding-left: 10px;
		// 		@media (max-width: 1024px){
		// 			padding-left: 0.977vw;
		// 		}
		// 		span {
		// 			opacity: 1;
		// 			color: #2a2a2a;
		// 		}
		// 	}
		// }
		&--calc {


			&:hover {
				.services__item-right {
					padding-left: 0px;

					@media (max-width: 1024px) {
						padding-left: 0vw;
					}

					span {
						opacity: 0.3;
					}
				}

				.services__item--calc__button {
					text-decoration: underline;
					text-decoration-thickness: 1px;
					text-underline-offset: 5px;
					text-decoration-skip: none;
					text-decoration-skip-ink: none;
				}
			}
		}
	}

	&__item {
		display: flex;
		align-items: flex-end;

		@media (max-width: 767px) {
			flex-wrap: wrap;
		}

		&-left {
			flex: 1 1 auto;

			@media (max-width: 1024px) {
				flex: 1 1 auto;
			}

			@media (max-width: 767px) {
				flex: 1 1 100%;
				margin-top: 6.667vw;
			}

			>span {
				display: block;
				max-width: 290px;
				margin-left: -20px;

				@media (max-width: 1919px) {
					margin-left: -15px;
					max-width: 240px;
				}

				@media (max-width: 1439px) {
					margin-left: -10px;
					max-width: 220px;
				}

				@media (max-width: 1024px) {
					margin-left: -0.977vw;
					max-width: 21.484vw;
				}

				@media (max-width: 767px) {
					max-width: 100%;
					margin-left: -2.667vw;
					max-width: 58.667vw;
				}

				padding-left: 20px;

				@media (max-width: 1919px) {
					padding-left: 15px;
				}

				@media (max-width: 1024px) {
					padding-left: 0.977vw;
				}

				@media (max-width: 767px) {
					padding-left: 2.667vw;
				}
			}

			&>span {
				font-family: 'Suisse Intl';
				font-style: normal;
				font-weight: 400;
				font-size: 18px;
				line-height: 21px;
				color: #2a2a2a;
				opacity: 0.4;

				span {
					white-space: nowrap;
					margin-right: 20px;
					display: inline-block;
				}

				b {
					display: inline-block;
					width: 5px;
				}

				@media (max-width: 1919px) {
					font-size: 14px;
					line-height: 16px;
				}

				@media (max-width: 1024px) {
					font-size: 1.367vw;
					line-height: 1.563vw;
				}

				@media (max-width: 767px) {
					font-size: 3.200vw;
					line-height: 3.733vw;
				}
			}
		}

		&-right {
			position: relative;
			flex: 0 1 65%;
			font-family: 'Suisse Intl';
			font-style: normal;
			font-weight: 400;
			font-size: 48px;
			line-height: 108.8%;
			letter-spacing: -0.03em;
			color: #2A2A2A;
			display: flex;
			justify-content: space-between;
			align-items: flex-end;
			transition: all 0.3s ease 0s;

			@media (max-width: 1919px) {
				flex: 0 0 64%;
				font-size: 36px;
				line-height: 108.8%;
			}

			@media (max-width: 1439px) {
				flex: 0 1 65%;
				font-size: 28px;
				line-height: 32px;
			}

			@media (max-width: 1024px) {
				flex: 0 1 65%;
				font-size: 2.734vw;
				line-height: 3.125vw;
			}

			@media (max-width: 767px) {
				order: -1;
				font-size: 5.333vw;
				line-height: 6.133vw;
			}

			span {
				opacity: 1;
				transition: all 0.5s ease 0s;

				@media (max-width: 1439px) {
					opacity: 1;
				}
			}
		}

		&-new {
			display: block;
			position: absolute;
			bottom: 30px;
			right: 0;
			background: #0047FF;
			border-radius: 30px;
			font-family: 'Suisse Intl';
			font-style: normal;
			font-weight: 400;
			font-size: 14px;
			line-height: 16px;
			text-transform: uppercase;
			color: #FFFFFF;
			padding: 6px 15px;

			@media (max-width: 1919px) {
				font-size: 9.39024px;
				line-height: 11px;
				padding: 4px 10px;
				bottom: 25px;
			}

			@media (max-width: 1024px) {
				font-size: 0.977vw;
				line-height: 0.977vw;
				padding: 4px 0.977vw;
				bottom: 2.441vw;
				border-radius: 2.930vw;
			}

			@media (max-width: 767px) {
				display: none;
			}
		}
	}
}
