.mb {
  &-5 {
    margin-bottom: 5px;
  }
  &-10 {
    margin-bottom: 10px;
  }
  &-20 {
    margin-bottom: 20px;
  }
}

.mt {
  &-5 {
    margin-top: 5px;
  }
  &-10 {
    margin-top: 10px;
  }
  &-20 {
    margin-top: 20px;
  }
}

.op {
  &-60 {
    opacity: 60%;
  }
  &-50 {
    opacity: 50%;
  }
}
.v-hidden {
  visibility: hidden;
}

body.body-overflow {
  overflow: hidden;
}
.hidden {
  display: none;
}
.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  .page {
    flex: 1 1 auto;
    padding: 129px 0 0;

    @media (max-width: 1919px) {
      padding: 100px 0 0;
    }

    @media (max-width: 1439px) {
      padding: 98px 0 0;
    }
    @media (max-width: 1024px) {
      padding: 6.836vw 0 0;
    }
    @media (max-width: 767px) {
      padding: vw(45,320) 0 0;
    }
    &-main,
    &-blog {
      padding: 0 !important;
    }
  }
  &.main-page {
    padding: 0 !important;

    > .page {
      padding: 0 !important;
    }
  }
}

.container {
  max-width: 100%;
  width: 100%;
  margin: 0 auto;
  padding: 0 30px;
  @media (max-width: 1919px) {
    padding: 0 20px;
  }
  @media (max-width: 1439px) {
    padding: 0 30px;
  }
  @media (max-width: 1024px) {
    padding: 0 2.93vw;
  }
  @media (max-width: 767px) {
    padding: 0 5.333vw;
  }
	&--gray{
		background: #F5F5F5;
		.general-intro{
			span{
				opacity: 1;
				color: #0047FF;
			}
		}
	}
}

.page-header {
  border-top: 1px solid rgba($color: #000000, $alpha: 0.2);
  padding-top: 30px;
  padding-bottom: 30px;
  min-height: 325px;
	&--long{
		min-height: 396px;
	}
  @media (max-width: 1919px) {
    min-height: 245px;
  }

  @media (max-width: 1024px) {
    border-top: 0.098vw solid rgba($color: #000000, $alpha: 0.2);
    padding-bottom: 10.742vw;
    min-height: 6vw;
  }
  @media (max-width: 767px) {
    border-top: 0.267vw solid rgba($color: #000000, $alpha: 0.2);
    padding-top: 8vw;
    padding-bottom: 8vw;
    min-height: 29.948vw;
  }
}

.general-title {
	@extend .h1;
  font-family: "Suisse Intl";
  color: #2a2a2a;

  span {
    color: #0047ff;
    font-weight: 400;
  }
  a {
    color: #0047ff;
    font-weight: 400;
    transition: all 0.5s ease 0s;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 6px;
      background-color: #0047ff;
      opacity: 0;
      transition: all 0.5s ease 0s;

      @media (max-width: 1919px) {
        height: 4px;
      }

      @media (max-width: 1439px) {
        height: 2px;
      }
      @media (max-width: 1024px) {
        height: 0.195vw;
      }
      @media (max-width: 767px) {
        height: 0.533vw;
      }
    }

    &:hover {
      &::before {
        opacity: 1;
      }
    }
  }
}
.general-intro{
	font-family: "Suisse Intl";
	font-style: normal;
	font-weight: 400;
	font-size: 48px;
	line-height: 108.8%;
	letter-spacing: -0.03em;
	color: #2A2A2A;
	max-width: 1068px;
	@media (max-width: 1919px) {
		font-size: 36px;
		line-height: 110%;
		max-width: 775px;
	}
	@media (max-width: 1439px) {
		font-size: 28px;
		line-height: 120%;
	}
	@media (max-width: 1024px) {
		font-size: 2.734375vw;
		line-height: 120%;
	}
	@media (max-width: 767px) {
		font-size: 6.25vw;
		line-height: 125%;
	}

	span {
		opacity: 0.45;
	}
}


.link-hv {
  transition: all 0.5s ease 0s;
  &:hover {
    color: #0047ff;
  }
}
.blue-btn {
  width: 595px;
  height: 65px;
  text-transform: uppercase;
  color: white;

  @media (max-width: 1919px) {
    width: 595px;
    height: 65px;
  }
  @media (max-width: 1439px) {
    width: 446px;
  }
  @media (max-width: 1024px) {
    width: 29.492vw;
    height: 6.445vw;
  }
  @media (max-width: 767px) {
    width: 100%;
    height: 12.8vw;
    flex: 1 1 100%;
  }
  cursor: pointer;
  background-color: #0047ff;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.5s ease 0s;

  &:hover {
    background-color: #0b3ec4;
  }

  span {
    font-family: "Suisse Intl";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    text-transform: uppercase;
    color: #ffffff;

    @media (max-width: 1919px) {
      font-size: 14px;
      line-height: 16px;
    }
    @media (max-width: 1024px) {
      font-size: 1.367vw;
      line-height: 1.563vw;
    }
    @media (max-width: 767px) {
      font-size: 3.2vw;
      line-height: 3.733vw;
    }
  }
}
.general-text {
  font-family: "Suisse Intl";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 20px;
  color: #2a2a2a;
  opacity: 0.5;
  @media (max-width: 1439px) {
    font-size: 14px;
  }
  @media (max-width: 1024px) {
    font-size: 1.367vw;
    line-height: 1.563vw;
  }
  @media (max-width: 767px) {
    font-size: 3.2vw;
    line-height: 4.533vw;
  }
}

.flex-lr {
  width: 100%;
  display: flex;
  justify-content: space-between;
  position: relative;
  @media (max-width: 767px) {
    flex-wrap: wrap;
  }
  .flex-lr__left {
    flex: 1 1 auto;
  }
  .flex-lr__right {
    flex: 0 0 65%;
		ul {
			li {
				position: relative;
				&::marker {
					content: "-"

				}
			}
		}
    @media (max-width: 767px) {
      flex: 1 0 100%;
    }
  }
}
body.noScroll {
	overflow-y: hidden;
}
.items-projects {
  display: flex;
  flex-wrap: wrap;
  margin: -20px -7.5px 0;
  @media (max-width: 1919px) {
    margin: -10px -5px 0;
  }
  @media (max-width: 1439px) {
    margin: -15px -7.5px 0;
  }
  @media (max-width: 1024px) {
    margin: -1.465vw -0.732vw 0;
  }
  @media (max-width: 767px) {
    margin: -4vw -2vw 0;
  }
  .item-projects {
    padding: 0 10px;
    flex: 0 1 50%;
    font-family: "Suisse Intl";
    font-style: normal;
    @media (max-width: 1919px) {
      padding: 0 5px;
    }
    @media (max-width: 1439px) {
      padding: 0 7.5px;
    }
    @media (max-width: 1024px) {
      padding: 0 0.732vw;
    }
    @media (max-width: 767px) {
      flex: 1 1 100%;
      padding: 0 2vw;
    }
    &.item-projects-dark {
      .item-projects__title {
        color: #ffffff;
      }
      .item-projects__text {
        color: #ffffff;
      }
    }
    &__content {
      margin-top: 20px;
      display: block;
      position: relative;
      min-height: 45vw;
      padding: 20px;
      overflow: hidden;
      @media (max-width: 1919px) {
        margin-top: 10px;
        min-height: 45vw;
      }
      @media (max-width: 1439px) {
        min-height: 45vw;
        margin-top: 15px;
      }
      @media (max-width: 1024px) {
        min-height: 47.07vw;
        margin-top: 1.465vw;
        padding: 1.953vw;
      }
      @media (max-width: 767px) {
        margin-top: 4vw;
        min-height: 97.067vw;
        padding: 2.667vw;
      }
      .item-projects-bronze {
        position: absolute;
        left: 30px;
        bottom: 30px;

        .page-header__labels {
          display: flex;

          .page-header__label {
            width: 128px;
            height: 128px;
            margin-right: 15px;

            @media (max-width: 1919px) {
              width: 96px;
              height: 96px;
            }
          }
        }

        @media (max-width: 1919px) {
          left: 15px;
          bottom: 15px;
        }
        @media (max-width: 1439px) {
          display: none;
        }
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
    &__header {
      position: relative;
      z-index: 2;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;
    }
    &__title {
      max-width: 50%;
      font-weight: 400;
      font-size: 48px;
      line-height: 108.8%;
      letter-spacing: -0.03em;
      color: #2a2a2a;
      transition: all 0.5s ease 0s;
      @media (max-width: 1919px) {
        max-width: 50%;
        font-size: 36.025px;
      }
      @media (max-width: 1439px) {
        font-size: 28px;
        line-height: 32px;
      }
      @media (max-width: 1024px) {
        font-size: 2.734vw;
        line-height: 3.125vw;
      }
      @media (max-width: 767px) {
        font-size: 5.333vw;
        line-height: 108.8%;
        max-width: 72%;
      }
    }

    &__btns {
      display: flex;
      align-items: center;
      position: absolute;
      top: 5px;
      right: 5px;
      @media (max-width: 1024px) {
        top: 0.488vw;
        right: 0.488vw;
      }
      @media (max-width: 767px) {
        flex-direction: column;
        align-items: flex-end;
        top: 1.333vw;
        right: 1.333vw;
      }
    }
    &__btn {
      font-weight: 400;
      font-size: 18px;
      line-height: 21px;
      text-align: right;
      color: #2a2a2a;
      background: #ffffff;
      border-radius: 25px;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 10px 15px;
      margin-right: 10px;
      @media (max-width: 1919px) {
        font-size: 14px;
        line-height: 16px;
        border-radius: 18.763px;
        padding: 8px 12px;
      }
      @media (max-width: 1439px) {
        border-radius: 25px;
        padding: 6px 10px;
      }
      @media (max-width: 1024px) {
        font-size: 1.367vw;
        line-height: 1.563vw;
        border-radius: 2.441vw;
        padding: 0.781vw 0.781vw;
        margin-right: 0.977vw;
      }
      @media (max-width: 767px) {
        font-size: 3.733vw;
        line-height: 4.267vw;
        margin-right: 0;
        border-radius: 6.667vw;
        padding: 1.6vw 2.667vw;
        &:not(:first-child) {
          margin-top: 1.333vw;
        }
      }
    }

    &__text {
      max-width: 50%;
      position: relative;
      z-index: 2;
      font-weight: 400;
      font-size: 18px;
      line-height: 21px;
      color: #2a2a2a;
      opacity: 0.5;
      flex: 1 1 100%;
      margin-top: 8px;
      br {
        display: none;
      }
      @media (max-width: 1919px) {
        font-size: 14px;
        line-height: 16px;
        margin-top: 12px;
      }
      @media (max-width: 1024px) {
        font-size: 1.367vw;
        line-height: 1.563vw;
        margin-top: 1.172vw;
      }
      @media (max-width: 767px) {
        font-size: 3.2vw;
        line-height: 3.733vw;
        margin-top: 3.2vw;
        display: none;
      }
    }

    &__img {
      display: flex;
      justify-content: center;
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: all 0.5s ease 0s;
      }
    }
    &:hover {
      .item-projects__title {
        /*color: #0047ff;*/
      }
      .item-projects__img {
        img {
          transform: scale(1.1);
        }
      }
    }
  }

  .item-projects-big {
    flex: 1 1 100%;
    .item-projects__img {
      .big {
        display: block;
      }
      .small {
        display: none;
      }
      @media (max-width: 767px) {
        .big {
          display: none;
        }
        .small {
          display: block;
        }
      }
    }
  }

  .item-projects-content-grey {
    background-color: #f4eaea;
  }
  .item-projects-content-grey-dark{
    background-color: #e4e4e4;
  }
  .item-projects-content-blue {
    background-color: #e3f0fe;
  }
  .item-projects-content-gold {
    background-color: #B9AF79;
  }

}

.color-black {
	color: #000000!important;
	* {
		color: #000000!important;
	}
}

.page .region {
  margin-bottom: 20px;
}

video {
  width: 100%;
  height: auto;
}

.paragraph--type--photos, .paragraph--type--videos {
  @extend .mt60;

  overflow: hidden;
  margin-left: -34px;
  margin-bottom: -34px;
  display: flex;
  flex-wrap: wrap;

  @media (max-width: 1439px) {
    margin-left: -25px;
    margin-bottom: -25px;
  }

  @media (max-width: 1024px) {
    margin-left: -1.66vw;
    margin-bottom: -1.66vw;
  }

  @media (max-width: 767px) {
    margin-left: 0;
    margin-bottom: -2.47vw;
  }

  a,video {
    height: auto;
    margin-left: 34px;
    margin-bottom: 34px;
    width: 0;
    flex-grow: 1;
    display: block;
    min-width: calc(16.66% - 34px); // 1/6  

    @media (max-width: 1439px) {
      margin-left: 25px;
      margin-bottom: 25px;
      min-width: calc(16.66% - 25px); // 1/6  
    }

    @media (max-width: 1024px) {
      margin-left: 1.66vw;
      margin-bottom: 1.66vw;
      min-width: calc(50% - 1.66vw); // 1/2
    }

    @media (max-width: 767px) {
      width: 100% !important;
      margin-left: 0;
      margin-bottom: 2.47vw;
      width: 100%;
    }

    img {
      width: 100%;
      height: auto;
    }
  }

  &-wrapper {
    display: flex;
    column-gap: 15px;

    @media (max-width: 1024px) {
      column-gap: 1.465vw;
    }

    @media (max-width: 767px) {
      flex-wrap: wrap;
      column-gap: 4vw;
    }

    .project-portfolio__item-img {
      flex: 0 1 50%;

      @media (max-width: 767px) {
        flex: 1 1 100%;

        &:not(:first-child) {
          margin-top: 4vw;
        }
      }
    }
  }

  & +.paragraph--type--videos,
  & +.paragraph--type--photos,
  & +.project-portfolio__item-img {
	  @extend .mt30;
  }

  + *:not(.paragraph--type--photos, .project-portfolio__item-img, .paragraph--type--videos):not(:first-child) {
    @extend .mt60;
  }
}


